import React from 'react';
import {NavLink} from 'react-router-dom';
import './Menu.css';


const vmenu = (props) =>{
    //note : Link/NavLink unlike anchor prevents the page from reloading but
    //allow react to handle the request on its own
    return (<div className='vmenu'>
        <NavLink  exact to={props.href} className={' menu'} activeClassName='selectedMenu'>
        {props.children}</NavLink></div>);
}



export default vmenu;