import React from 'react';
import angCss from './Angular.module.css';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';

class SimpleAngular extends React.Component{
    render(){
        return <div className={cssClasses.container}>  
        <div className={cssClasses.blockTitle}>Beginner Level</div>
        <div className={cssClasses.codeBody}>
            <div>
                <span className={angCss.tag}>{'<h2>'}</span>
                <span>{'Our Products'}</span>
                <span className={angCss.tag}>{'</h2>'}</span>
            </div>
            <div>
               <span className={angCss.tag}>{'<div '}</span>
               <span className={angCss.dir}>{' *ngFor'}</span> = 
               <span className={angCss.command}>{"\" let product of products \""}</span>
               <span className={angCss.tag}>{'>'}</span>
            </div>
            <div>
                <span className={angCss.tag}>{'<h3>'}</span>
                <span className={angCss.insert}>{'{{product.name}}'}</span>
                <span className={angCss.tag}>{'</h3>'}</span>
            </div>
            <div>
              <span className={angCss.tag}>{'</div>'}</span>
            </div>
        </div>
        </div>
    }
}

export default SimpleAngular;