import React,{Component} from 'react';
import './Layout.css';
import Menus from '../Menus/Menus';
import CompanyName from '../CompanyName/CompanyName';
import VMenus from '../Menus/VMenus';


class Layout extends Component{
    render()
    {
       return <React.Fragment>
           <div id='header'>  
           <div className='vmenusCont'><VMenus/> </div>
           <div className='compNameCont'><CompanyName/> </div>
           <div className='hmenusCont'><Menus/></div>
          </div>

           <div id="content">{this.props.children}</div>

           <div id='footer'>Website designed and supported by Sawari Software Limited (www.sawarisoftware.com)</div>
       </React.Fragment>
    }
}

export default Layout;