import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';
import cssCss from '../CSS/Css.module.css';
import sassCss from './Sass.module.css';

class SimpleSass extends React.Component{

    render(){
       return <div className={cssClasses.container}>
              <div className={cssClasses.blockTitle}>Beginner Level</div>
              <div className={cssClasses.codeBody}>
              
              <span className={sassCss.comment}>{'/* declare and assign values to variables */'}</span><br/>
              <span className={cssCss.element}>{'$backgroundColor '}</span> : 
              <span className={cssCss.value}>{'black'}</span> ; <br/>
              <span className={cssCss.element}>{'$textColor '}</span> : 
              <span className={cssCss.value}>{'white'}</span> ; 
             
              <br/><span className={cssCss.element}>{'$fontFamily '}</span> : 
              <span className={cssCss.value}>{'verdana'}</span> ; <br/>
              <span className={cssCss.element}>{'$fontSize '}</span> : 
              <span className={cssCss.value}>{'20px'}</span> ; <br/> 
              <br/>
              <span className={sassCss.comment}>{'/* use variables your css */'}</span><br/>
              <span>{'body {'}</span> <br/>
              <span className={cssCss.element}>{'background-color '}</span> : 
              <span className={cssCss.value}>{'$backgroundColor'}</span> ; <br/>
              <span className={cssCss.element}>{'color '}</span> : 
              <span className={cssCss.value}>{'$textColor'}</span> ; <br/>
              <span className={cssCss.element}>{'font-family '}</span> : 
              <span className={cssCss.value}>{'$fontFamily'}</span> ; <br/>
              <span className={cssCss.element}>{'font-size '}</span> : 
              <span className={cssCss.value}>{'$fontSize'}</span> ; <br/>
              <br/><span>{'}'}</span>
              
              </div>
              </div>
             
            
    }

}

export default SimpleSass;