import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';
import html5Css from  '../Html5/Html5.module.css';
import pugCss from './Pug.module.css';

const simplePug = (props) =>{

     return <div className={cssClasses.container}>  
     <div className={cssClasses.blockTitle}>Beginner Level</div>
     <div className={cssClasses.codeBody}>
        <span className={''}>{'doctype html'}</span> <br/>
        <span className={''}>{'html'}</span><br/>
        <span className={pugCss.indent1}>{'head'}</span><br/>
        <div className={html5Css.content}>
         <span className={pugCss.indent2}>{'title'}</span>
         <span className={pugCss.indent3}>{'Welcome Page'}</span><br/>
        
        </div>
      
       
        <span className={pugCss.indent1}>{'body'}</span><br/>
        <div className={html5Css.content}>
        <span className={pugCss.indent2}>{'h1'}</span>
        <span className={pugCss.indent3}>{'Welcome to Heaven.'}</span><br/>
        <span className={pugCss.indent2}>{'p'}</span>
        <span className={pugCss.indent3}>{'Please have all you desire.'}</span><br/>
        </div>
        
     </div> </div>
}

export default simplePug;