import React,{Component} from 'react';
import cssClasses from '../CodeBlocks/JavaScriptComp/JavaScriptComp.module.css';

class CodePlayerComp extends Component{

    state = {codes:this.props.codeBlocks, currentIndex:0};

    render(){

        setTimeout(()=>{
            if(this.state.currentIndex<3)
            {
                var curIndex = this.state.currentIndex+1;
                this.setState({currentIndex:curIndex});
            }
            else
            {
                clearTimeout();
            }

        },5000);

        return <React.Fragment>
          {this.state.currentIndex < this.state.codes.length ? this.state.codes[this.state.currentIndex] : 
          this.state.codes[this.state.codes.length-1] }
        </React.Fragment>
    }
}

export default CodePlayerComp;