import React,{Component} from 'react';
import {connect} from 'react-redux';

class Buttons extends Component{

    render(){
       return <React.Fragment>
                 <label style={{display:'block'}}>{this.props.val?this.props.val:0}</label>
                <button onClick={this.props.onIncrement}>Increment</button>
                <button onClick={this.props.onDecrement}>Decrement</button>
                <button onClick={this.props.onAddFive}>Add 5</button>
                <button onClick={this.props.onSubtractTwo}>Subtract 2</button>
       </React.Fragment>
   
    }
}

const mapStateToProps = (state) => {
   return{
    val: state.counter
   } 
}

const mapDispatchToProps = (dispatch) =>{
    return {
             onIncrement : ()=> dispatch({type:'INC'}),
             onDecrement : ()=> dispatch({type:'DEC'}),
             onAddFive : ()=> dispatch({type:'ADD5'}),
             onSubtractTwo : ()=> dispatch({type:'SUB2'}) 
           }
}

export default connect(mapStateToProps,mapDispatchToProps)(Buttons);