import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Menus from './Menus/Menus';
import Login from './Login/Login';
import Courses from './Courses/Courses';
import Buttons from '../src/Buttons/Buttons';
import Codes from '../src/CodeBlocks/CodeBlocks';
import SingleCourse from '../src/Courses/SingleCourse/SingleCourse'
import Test from '../src/Test/Test';
import SignUp from './Login/SignUp/SignUp';
import StudentDashboard from './Login/StudentDashboard/StudentDashboard';
import Home from './Home/Home';
import Contact from './Contact/Contact';
import Tutorial from './Login/StudentDashboard/Tutorial/Tutorial';

import {BrowserRouter,Route}  from 'react-router-dom';

class App extends Component {
  render() {
    return (
      <BrowserRouter>
         <div className="App">
         <Route path="/" exact component={Home}/>
         <Route path="/courses" exact component={Courses}/>
         <Route path="/course" exact component={SingleCourse}/>
         <Route path="/contact" exact component={Contact}/>
         <Route path="/menus" exact component={Menus}/>
         <Route path="/login" exact component={Login}/>
         <Route path="/buttons" exact component={Buttons}/>
         <Route path = "/codes" exact component={Codes}/>
         <Route path='/signup' exact component={SignUp}/>
         <Route path='/studdashboard' exact component ={StudentDashboard}/>
         <Route path='/tutorial' exact component={Tutorial}/>
         <Route path = "/test" exact component={Test}/>
      </div>

      </BrowserRouter>
      
    );
  }
}

export default App;
