import React from 'react';
import Menu from './Menu/Menu';
import cssClasses from '../Menus/Menus.module.css';
import MenuItems from './MenuItems'

class Menus extends React.Component{

    state = {selectedMenu:0,
    menus:MenuItems};

    render()
    {
      return <div className={cssClasses.rightPos}>
        {this.state.menus.map((val,index,arr)=>{
        return <Menu href={val.link} menuId={val+index} key={val+index}>
            {val.text}</Menu>
        })}
        </div>
    }
   
}

export default Menus;