import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';
import html5Css from '../Html5/Html5.module.css';
import cssCss from './Css.module.css';

class SimpleCss extends React.Component{

    render(){
       return <div className={cssClasses.container}>
              <div className={cssClasses.blockTitle}>Beginner Level</div>
              <div className={cssClasses.codeBody}>
              
              <span className={html5Css.tag}>{'<style>'}</span><br/>
              <span>{'body {'}</span> <br/>
              <span className={cssCss.element}>{'background-color '}</span> : 
              <span className={cssCss.value}>{'black'}</span> ; <br/>
              <span className={cssCss.element}>{'color '}</span> : 
              <span className={cssCss.value}>{'white'}</span> ; 
              <br/><span>{'}'}</span>

              <br/><span>{'p {'}</span> <br/>
              <span className={cssCss.element}>{'font-family '}</span> : 
              <span className={cssCss.value}>{'verdana'}</span> ; <br/>
              <span className={cssCss.element}>{'font-size '}</span> : 
              <span className={cssCss.value}>{'20px'}</span> ; 
              <br/><span>{'}'}</span>

              <br/><span>{'h1 {'}</span> <br/>
              <span className={cssCss.element}>{'text-align '}</span> : 
              <span className={cssCss.value}>{'center'}</span> ; <br/>
              <span>{'}'}</span>


              <br/><span className={html5Css.tag}>{'</style>'}</span>
              </div>
              </div>
             
            
    }

}

export default SimpleCss;