import React,{Component} from 'react';
import Layout from '../../Layout/Layout';
import cssClasses from './SignUp.module.css';
import axios from 'axios';

const serverUrl = "odeapp-env.3s9ka22rdf.us-east-2.elasticbeanstalk.com";

class SignUp extends Component{
    render(){
        return <Layout> 
            <div className={cssClasses.container}>
              
              <div id='formDiv' className={cssClasses.suTitle}>Sign Up with SSPA</div> 
              <form id='mainForm' method='post' action='' className={cssClasses.form}>
                 <label id='status' className={cssClasses.sendStatus}></label>
                 <input type='text' id='ufullname' placeholder='Your Name' onClick={()=>this.resetInput('ufullname')}/>
                 <input type='email' id='uemail' placeholder='Your Email Address' onClick={()=>this.resetInput('uemail')} />
                 <input type='password' id='upassword' placeholder='Password' onClick={()=>this.resetInput('upassword')}/>
                 <input type='password' id='cpassword' placeholder='Confirm Password' onClick={()=>this.resetInput('cpassword')}/>
                 <input type='submit' id='sendBtn' value='Create Account' 
                 className={cssClasses.subBtn} onClick={(event)=>this.sendConfirmationEmail(event)}/>
              </form>
              
            </div>
            
            </Layout> 
    }
    resetInput(id)
    {
        let elem = document.getElementById(id);
        elem.value = "";
        elem.className = ""; //to be replaced by any set style
    }
    sendConfirmationEmail(event){
         event.preventDefault();
         //clear send status
         document.getElementById('status').innerText="";

         let fullname='',email='',password='';
         //validate input
         let elem = document.getElementById('ufullname');
         fullname = elem.value;
         if(fullname.trim()==="")
         {
             elem.className+=" "+cssClasses.invalidInput;
             return;
         }
         elem = document.getElementById('uemail');
         email = elem.value;
         if(email.trim()==="" || !this.isValidEmail(email,'uemail'))
         {
             elem.className+=" "+cssClasses.invalidInput;
             return;
         }
         elem = document.getElementById('upassword');
         password=elem.value;
         if(password.trim()==="")
         {
             elem.className+=" "+cssClasses.invalidInput;
             return;
         }
         if(elem.value.trim()!==document.getElementById('cpassword').value.trim())
         {
            elem.className+=" "+cssClasses.invalidInput;
            document.getElementById('cpassword').className+=" "+cssClasses.invalidInput;
            return;
         }
         //show page busy sending data
         let btnCss =  document.getElementById('sendBtn').className;
         document.getElementById('sendBtn').className+=" "+cssClasses.showBusy;
         //process form data
         let userInfo = {fullname:fullname,email:email,password:password};
         axios.post(serverUrl+'/sendconfirmation',userInfo)
         .then(response=>{
             
             if(response.status===201)
             {
                 //success
                 document.getElementById('status').className = 
                 cssClasses.sendStatus+" "+cssClasses.showSuccess;
                 document.getElementById('status').innerText = "Please check your inbox for confirmation email.";
                 //reset form inputs
                 let inps = ['ufullname','uemail','upassword','cpassword'];
                 for(let i=0; i<inps.length; i++)
                 document.getElementById(inps[i]).value="";

                  //indicate end of processing
                 document.getElementById('sendBtn').className = btnCss;
                 return;
             }
             //handle failure here
             document.getElementById('status').className = 
             cssClasses.sendStatus+" "+cssClasses.showFailure;
             document.getElementById('status').innerText = "Sorry, there was problem sending email. Try again later.";
             document.getElementById('sendBtn').className = btnCss;
              //indicate end of processing
              document.getElementById('sendBtn').className = btnCss;
         })
         .catch(err=>{
            //handle failure here
             document.getElementById('status').className = 
             cssClasses.sendStatus+" "+cssClasses.showFailure;
             document.getElementById('status').innerText = "Sorry, there was problem sending email. Try again later.";
             document.getElementById('sendBtn').className = btnCss;
              //indicate end of processing
              document.getElementById('sendBtn').className = btnCss;
         });

    }
    isValidEmail(email,id){

        let arr = email.split('@');
        let elem = document.getElementById(id);
        if(arr.length<2)
        {
            elem.className+=" "+cssClasses.invalidInput;
            return false;
        }
        arr = arr[1].split('.');
        if(arr.length<2)
        {
            elem.className+=" "+cssClasses.invalidInput;
            return false;
        }
        //check that last two items are not empty
        if(arr[arr.length-1].trim()===""||arr[arr.length-2].trim()==="")
        {
            elem.className+=" "+cssClasses.invalidInput;
            return false; 
        }

        return true;
    }
}

export default SignUp;
