import React,{Component} from 'react';
import cssClasses from './JavaScriptComp.module.css';
import DownArroy from './Arrows/DownArroy';
import SimpleCode from './SimpleCode';
import MediumCode from './MediumCode';
import ComplexCode from './ComplexCode';
import CodePlayer from '../CodePlayer';

class JavaScriptComp extends Component{

    render(){ 
        let  codeBlocks = [<SimpleCode/>,<MediumCode/>,<ComplexCode/>];
        return <CodePlayer codeBlocks={codeBlocks}/>
    }
}

export default JavaScriptComp;