import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';
import pyCss from './Python.module.css';
import goCss from '../GO/SimpleGo.module.css';

const simplePy = (props) => {
      return <div className={cssClasses.container}>
        <div className={cssClasses.blockTitle}>Beginner Level</div>
        <code>
        <div  id="simple" className={cssClasses.codeBody}>

        <div className={cssClasses.comment}>{'# file name : hello.py'}</div>
        <div>
            <span className={goCss.package}>import </span> random
        </div>
        <div>   
        <span className={cssClasses.funcName}>print</span>(
        <span className={pyCss.text}>'Hello, Genius!'</span>)
        </div>
        <div>
            <span className={cssClasses.keyword}>if </span>random.<span className={cssClasses.funcName}>randint</span>
            (<span className={goCss.text}>1,100</span>)%2 == 0:
        </div>
        <div className={pyCss.indent1}>
            <span className={cssClasses.funcName}>print</span>(
            <span className={pyCss.text}>'Today is your lucky day.'</span>)
        </div>
        <div>
            <span className={cssClasses.keyword}>else</span>:
        </div>
        <div className={pyCss.indent1}>
            <span className={cssClasses.funcName}>print</span>(
            <span className={pyCss.text}>'Please be careful.'</span>)
        </div>

        </div>
        </code>
      </div>
   
}

export default simplePy;

