import React,{Component} from 'react';
import './Simple.css';
import {NavLink}  from 'react-router-dom';
import StudentDashboard from '../StudentDashboard/StudentDashboard';
import axios from 'axios';

const serverUrl = "Nodeapp-env.3s9ka22rdf.us-east-2.elasticbeanstalk.com";

class Simple extends Component{

    state = {currentDisplay:null};

    componentDidMount(){
        this.setState({currentDisplay:<div id='main'>
        <div>
         <form action='' 
        className = "w3-container w3-card-4 w3-light-grey w3-margin">
           <h2 className="highLighted formHeader w3-center">{this.props.title}</h2>
           <div className='loginAd'>Journey with us towards greatness.</div>
           <input type="text" id='uname' className="w3-input linput" placeholder="username"/>
           <input type="password" id='upass' className="w3-input linput " placeholder="password"/>
           <span className='loginErrMsg' id='lgErMsg'>Invalid Username and/or Password.</span>
           <input type="button" className="highLighted w3-button w3-border w3-round-xxlarge fillWidth"  
           value="Login Now" onClick={()=>this.processLogin(this)}></input> 
           <NavLink to='/signup' className='signUpLink'>Sign Up</NavLink>
        </form>
        </div>
    </div>});

    }
  
    processLogin(caller){
          let username = document.getElementById('uname').value;
          let upassword = document.getElementById('upass').value;
          //to be replaced by request to server for authenticating
          axios.post(serverUrl+'/userlogin/',{username:username,password:upassword})
          .then(response=>{
                 //show student dashboard
              if(response.status===201)
              {
                caller.setState({currentDisplay:<StudentDashboard userid={username}/>}); 
                return;
              }
              //show invalid login messages.
             document.getElementById('lgErMsg').style.display='block';
             
          })
          .catch(err=>{
             //show invalid login messages.
             document.getElementById('lgErMsg').style.display='block';
          });
    }

    render() {
        return (this.state.currentDisplay);
    }
}



export default Simple;