import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';
import djCss from './Django.module.css';

const simpleDj = (props) => {
      return <div className={cssClasses.container}>
        <div className={cssClasses.blockTitle}>Beginner Level</div>
        <code>
        <div  id="simple" className={cssClasses.codeBody}>

        <div >
          <span className={djCss.keyword}>{'from '}</span>
          <span className={djCss.obj}> django.http </span>
          <span className={djCss.keyword}>{' import '}</span> 
          HttpResponse 
        </div>

        <div>
            <span className={djCss.keyword}>def </span>
            <span className={djCss.func}>index</span>(
            <span>request</span>) :
        </div>

        <div className={djCss.indent1}>   
        <span className={djCss.keyword}>return </span>
        <span>HttpResponse</span>(
        <span className={djCss.text}>"Hello, world!"</span>)
        </div>

        </div>
        </code>
      </div>
   
}

export default simpleDj;

