import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';
import cssCss from './Css.module.css';
import html5Css from '../Html5/Html5.module.css';

class MediumCss extends React.Component{

    render(){
       return <div className={cssClasses.container}>
              <div className={cssClasses.blockTitle}>Intermediate Level</div>
              <div className={cssClasses.codeBody}>
    <span className={html5Css.tag}>{'<html>'}</span><br/>
    <span className={html5Css.tag}>{'<head>'}</span><br/>
    <span className={html5Css.tag+" "+cssCss.indent1}>{'<style>'}</span><br/>

    <span className={cssCss.indent2}>{'.mainDiv { '}</span><br/>
    <span className={cssCss.element+" "+cssCss.indent2}>{'display '}</span> : 
    <span className={cssCss.value}>{'flex'}</span> ; <br/>
    <span className={cssCss.element+" "+cssCss.indent2}>{'flex-direction '}</span> : 
    <span className={cssCss.value}>{'row'}</span> ;<br/>
    <span className={cssCss.element+" "+cssCss.indent2}>{'flex-wrap '}</span> : 
    <span className={cssCss.value}>{'wrap'}</span> ; <br/>
    <span className={cssCss.element+" "+cssCss.indent2}>{'justify-content '}</span> : 
    <span className={cssCss.value}>{'left'}</span> ;  
    <br/><span className={cssCss.indent2}>{'}'}</span>

    <br/><span className={cssCss.indent2}>{'img { '}</span><br/>
    <span className={cssCss.element+" "+cssCss.indent2}>{'float '}</span> : 
    <span className={cssCss.value}>{'left'}</span> ; <br/>
    <span className={cssCss.element+" "+cssCss.indent2}>{'clear '}</span> : 
    <span className={cssCss.value}>{'left'}</span> ; <br/>
    <span className={cssCss.element+" "+cssCss.indent2}>{'overflow '}</span> : 
    <span className={cssCss.value}>{'auto'}</span> ; <br/>
    <span className={cssCss.indent2}>{'}'}</span>

    <br/><span className={html5Css.tag}>{'<link '}</span>
    <span className={cssCss.element}>{'rel '}</span> = 
    <span className={cssCss.value}>{"'stylesheet'"}</span>
    <span className={cssCss.element}>{'type '}</span> = 
    <span className={cssCss.value}>{"'text/css'"}</span>  
    <span className={cssCss.element}>{'href '}</span> = 
    <span className={cssCss.value}>{"'mainstyle.css'>"}</span><br/> 
    

    <br/><span className={html5Css.tag+" "+cssCss.indent1}>{'</style>'}</span><br/>
    <span className={html5Css.tag}>{'</head>'}</span><br/>
    <span className={html5Css.tag}>{'<body>'}</span><br/>
    <span className={html5Css.tag}>{'</body>'}</span><br/>
    <span className={html5Css.tag}>{'</html>'}</span> <br/>
              </div>
              </div>
    }

}

export default MediumCss;