import React from 'react';
import '../../../src/w3.css';
import {NavLink,Redirect} from 'react-router-dom';
import classes from './Course.module.css';


class Course extends React.Component {

     state = {defaultMount:null,subjectClicked:false,subject:''};

     componentDidMount(){

      let subjects = this.props.detail.toString().split('~');
      let courseName = this.props.course;
      let preq = this.props.prereq;  
      if(preq===undefined || preq===null || parseInt(preq)===0)
        preq = null;
      else
       preq = <div className={classes.prereq}>{'Pre-requisite : '+ preq}</div>; 

      this.setState({ defaultMount: <div className={"w3-card-2 "+classes.Course}>
          
          <div className={classes.title}>
              {this.props.course}
          </div>
          <div className={classes.belowTitle}></div>
          {/*list of subjects */}
          <ul className={'subList'}>
            {subjects.map(subject=><li key={subject} id={subject} 
            onMouseOver={()=>this.canClick(subject,this)}
            onClick={()=>this.moveToSubject(subject,this)}>{subject}</li>)}
          </ul>
         
          {/* link to details page */}
          { this.props.showLink===false? null :
          <div className={classes.navlink}>
          <NavLink  exact to={{
            pathname:'/course',
            courseInfo : {
            subjects:{subjects},
            courseName:{courseName}}}}>click here for more...</NavLink>
            </div>
          }

           {/* show pre-requisite course */}
           {preq}
  
      </div>
      });

     }
     
     canClick(id,caller){
       if(caller.props.showLink===false)
        document.getElementById(id).className=classes.clickable;

     }

     moveToSubject(subject,caller){
      if(caller.props.showLink===false)
         caller.setState({subject:subject,subjectClicked:true}); 
     }

    //get subject of course from db
    render(){
       return this.state.subjectClicked===true  ? <Redirect 
       to={{pathname:'/tutorial',state:{subject:this.state.subject}}}/> : 
       this.state.defaultMount
    }
    
  }

  

export default Course;