import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';

const mediumCode = (props) =>{
    return <div className={cssClasses.container}>
         <div className={cssClasses.blockTitle}>Intermediate Level</div>
        <code>
        <div className={cssClasses.codeBody} id="medium">
           <span className={cssClasses.modifier}>function</span>
           <span className={cssClasses.funcName}>computeAverage</span>(numbers)<br/>
           <span style={{textAlign:'left'}}>{"{"}</span><br/> 
           <span style={{marginLeft:'20px'}}> <span className={cssClasses.modifier}>let</span> total = 0, average = 0, i = 0 ;</span><br/>
           <span style={{marginLeft:'20px'}}> if(numbers.length == 0)</span><br/>
           <span style={{marginLeft:'30px'}}><span className={cssClasses.return}>return</span> 0 ;</span><br/>
           <span style={{marginLeft:'20px'}}> {" for(i = 0; i < numbers.length; i++)"} </span><br/>
           <span style={{marginLeft:'30px'}}> total = total + numbers[i];</span><br/>
           <span style={{marginLeft:'20px'}}> <span className={cssClasses.return}>return</span> total/numbers.length;</span><br/>
           <span>{"}"}</span>
        </div>

        </code>
    </div>
}

export default mediumCode;