import React,{Component} from 'react';
import Layout from '../Layout/Layout';
import MessageBlock from '../MessageBlock/MessageBlock';
import cssClasses from './Home.module.css';
import TEXT from './hometxt';

class Home extends Component{

    state={messages:TEXT};

    render(){
        return <Layout>
            
            <div className={cssClasses.welcomeDiv}>
                Welcome to Sawari Software Programming Academy (SSPA)</div>
            <div className={cssClasses.msgContainer}>
            {this.state.messages.map((val,index,arr)=>{
                return <MessageBlock key={val+index} width={val.width} height={val.height}
                title={val.title} message={val.message} linkTo={val.linkTo}
                linkMsg={val.linkMsg}/>
            })}
            </div>
           
             
        </Layout>
    }
}

export default Home;
