import React from 'react';
import Course from './Course/Course';
import '../../src/w3.css';
import classes from './Courses.module.css';
import Layout from '../Layout/Layout';
const axios = require('axios');

const serverUrl = "Nodeapp-env.3s9ka22rdf.us-east-2.elasticbeanstalk.com";

class Courses extends React.Component{
  
  state = {coursesDetail:[],coursesDict:[]};
  async componentDidMount(){
      
         //get courses if they were not loaded previously
         if(this.state.coursesDetail.length>0)
             return;
           axios.get(serverUrl+"/courses/")
           .then(response =>{
             //format courses data from db. Note data is table : course_id,name,preq_id,subject.
             let prevCourseId = response.data.coursesDetail[0].course_id;
             let lcourses = [];
             let subjects = [];
             for(let i=0; i<response.data.coursesDetail.length; i++)
             {
                 if(response.data.coursesDetail[i].course_id===prevCourseId)
                     subjects.push(response.data.coursesDetail[i].subj_name);
                 else
                 {
                    lcourses.push({course_id:prevCourseId,subjects:subjects});
                    subjects = [response.data.coursesDetail[i].subj_name];
                 }
                 prevCourseId = response.data.coursesDetail[i].course_id;
             }
             if(subjects.length>0)
              lcourses.push({course_id:prevCourseId,subjects:subjects});
             //put coursesDict into hashmap
              let lcoursesdict = [];
              for(let i=0; i<response.data.coursesDict.length;i++)
                lcoursesdict[response.data.coursesDict[i].course_id] = response.data.coursesDict[i].name+
                "~"+response.data.coursesDict[i].prereq_id;
                 
             this.setState({coursesDetail:lcourses,coursesDict:lcoursesdict});
           })
           .catch(error=>console.log("sspa error :"+error));
           
  }

  render()
  {
    return <Layout>
    <div className={classes.header}>courses offered by SSPA</div>
   <div className={classes.Courses}>
     {this.state.coursesDetail.map((singleCourse,index, arr) =>{
       //find course name
       let courseName = this.state.coursesDict[singleCourse.course_id].split('~')[0];
       let preq = this.state.coursesDict[singleCourse.course_id].split('~')[1];
       if(parseInt(preq)!==0)
        preq = this.state.coursesDict[parseInt(preq)].split('~')[0];
      
       return <React.Fragment>
          <Course key={courseName+index} course={courseName} 
          detail={singleCourse.subjects.join("~")} 
          prereq={parseInt(preq)!==0?preq:null}/>
       </React.Fragment> 
     })}
    </div>
    </Layout>;
  }

}

export default Courses;