import React from 'react';
import cssClasses from './CodeBlock.module.css';

class CodeBlock extends React.Component{

    render(){
        return <div className={cssClasses.main}>
            <div className={cssClasses.block}>
             ...1
             <div className={cssClasses.block}>...2</div>
             <div className={cssClasses.block}>...2....................................
                <div className={cssClasses.block}>...3...............</div>
                <div className={cssClasses.block}>...3</div>
             </div>

            </div>
        </div>
    }

}

export default CodeBlock; 