import React from 'react';
import cssClasses from './CompanyName.module.css';

const companyName = (props) =>{

    return <div className={cssClasses.mainDiv}>
        <code className={cssClasses.codeDecorator}>++</code>
        <span className={cssClasses.compName}>SSPA</span>
        <code className={cssClasses.codeDecorator}>--</code>
    </div>
}

export default  companyName;