import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';
import html5Css from  '../Html5/Html5.module.css';

const simple = (props) =>{

     return <div className={cssClasses.container}>  
     <div className={cssClasses.blockTitle}>Beginner Level</div>
     <div className={cssClasses.codeBody}>
        <span className={html5Css.tag}>{'<!DOCTYPE html>'}</span> <br/>
        <span className={html5Css.tag}>{'<html>'}</span><br/>
        <span className={html5Css.tag}>{'<head>'}</span><br/>
        <div className={html5Css.content}>
         <span className={html5Css.tag}>{'<title>'}</span>
         <span >{'Page Title'}</span>
         <span className={html5Css.tag}>{'</title>'}</span><br/>
        </div>
      
        <span className={html5Css.tag}>{'</head>'}</span><br/>
        <span className={html5Css.tag}>{'<body>'}</span><br/>
        <div className={html5Css.content}>
        <span className={html5Css.tag}>{'<h1>'}</span>
        <span>{'This is a Heading.'}</span>
        <span className={html5Css.tag}>{'</h1>'}</span><br/>
        <span className={html5Css.tag}>{'<p>'}</span>
        <span >{'This is a paragraph.'}</span>
        <span className={html5Css.tag}>{'</p>'}</span><br/>
        </div>
        <span className={html5Css.tag}>{'</body>'}</span><br/>
        <span className={html5Css.tag}>{'</html>'}</span>
     </div> </div>
}

export default simple;