import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';
import html5Css from  '../Html5/Html5.module.css';
import vueCss from './Vue.module.css';

const simple = (props) =>{

     return <div className={cssClasses.container}>  
     <div className={cssClasses.blockTitle}>Beginner Level</div>
     <div className={cssClasses.codeBody}>
       
        <div className={html5Css.content}>
        <div>
            <span className={vueCss.tag}>{"<div id='mp'>"}</span>
        </div>
        <div>
        <span>{'<span>'}</span>
        <span className={vueCss.val}>{'{{ message }}'}</span> 
        <span>{'<span>'}</span>
        </div>
        <div>   
          <span className={vueCss.tag}>{'</div>'}</span>
        </div>
        
        <div>
         <div className={vueCss.tag}>{'<script>'}</div>
         <div>
             <span className={vueCss.keyword}>{'var '}</span>
             <span className={vueCss.obj}>{' appObj '}</span> = 
             <span className={vueCss.keyword}>{' new '}</span>
             <span className={vueCss.obj}>{' Vue'}</span>
             <span className={vueCss.brace}>{'({'}</span><br/>
         </div>
         <div>
          <span className={vueCss.obj}>{' el:'}</span>
          <span className={vueCss.data}>{" '#mp'"}</span> ,<br/>
          <span className={vueCss.obj}>{'data : {message : '}</span>
          <span className={vueCss.data}>{" 'Welcome to Vue Js'"}</span>
          <span className={vueCss.obj}>{'}'}</span>
         </div>
         <div className={html5Css.tag}>{'</script>'}</div>
        </div>
        
        </div>
     
     </div> </div>
}

export default simple;