import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';
import goCss from './SimpleGo.module.css';

const simpleGo = (props) => {
      return <div className={cssClasses.container}>
        <div className={cssClasses.blockTitle}>Beginner Level</div>
        <code>
        <div  id="simple" className={cssClasses.codeBody}>
        <span className={goCss.package}>package </span><span> main</span><br/>   
        <span className={cssClasses.keyword}>import </span>
        <span> {' "fmt" '} </span><br/>
        <span className={cssClasses.keyword}>func</span> <span className={cssClasses.funcName}> main</span>()<br/>
        <span>{"{"}</span>
        <div style={{paddingLeft:'30px'}}>
            fmt.<span className={cssClasses.funcName}>Println</span>(
            <span className={goCss.text}>{'"Hello, World!"'}</span>)
         </div>
         <span>{'}'}</span>
        </div>
        </code>
      </div>
   
}

export default simpleGo;

