import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';

const simpleCode = (props) => {
      return <div className={cssClasses.container}>
        <div className={cssClasses.blockTitle}>Beginner Level</div>
        <code>
        <div  id="simple" className={cssClasses.codeBody}>
        <span className={cssClasses.funcName}>alert</span><span>('Hello World!');</span><br/>   
        <span className={cssClasses.modifier}>var</span>
        <span> message = 'I have arrived.';</span>
        <div style={{paddingLeft:'30px'}}>  console.<span className={cssClasses.funcName}>log</span>(message);</div>
        </div>
        </code>
      </div>
   
}

export default simpleCode;