import React from 'react';
import {NavLink} from 'react-router-dom';
import cssClasses from './MessageBlock.module.css';


const messageBlock = (props) =>{
    return <div className={cssClasses.blockDiv+" "} style={{width:props.width,height:props.height}}>

<div className={cssClasses.blockTitle}>{props.title}</div>

<div className={cssClasses.message}>{props.message}</div>

<div className={cssClasses.link}>
<NavLink to={props.linkTo}>
    {props.linkMsg===undefined?'click here for more...':props.linkMsg}</NavLink>
</div>

</div>
}

export default messageBlock;
