import React from 'react';
import SimpleCss from './SimpleCss';
import MediumCss from './MediumCss';
import CodePlayer from '../CodePlayer';

class CssComp extends React.Component{
    render(){
        let codeBlocks = [<SimpleCss/>,<MediumCss/>];
        return <CodePlayer codeBlocks={codeBlocks}/>
    }
}

export default CssComp;