import React from 'react';
import Layout from '../Layout/Layout';
import cssClasses from './Contact.module.css';
import locationImg from './images/location.png';
import telephoneImg from './images/telephone.png';
import whatappImg from './images/whatapp.png';
import emailImg from './images/email.png';
import axios from 'axios';

const serverUrl = "Nodeapp-env.3s9ka22rdf.us-east-2.elasticbeanstalk.com";

class Contact extends React.Component{

    state = {inputCssClasses:'',name:'',email:'',phone:'',message:''};

    checkNsubmit(event,caller){

        //prevent browser from submitting form (default behavior)
           event.preventDefault();
           //show busy cursor
           let btnCss = document.getElementById('sendBtn').className;
           document.getElementById('sendBtn').className+=" "+cssClasses.showBusy;

        //reset delivery status (whether msg delivered successfully or not)
           document.getElementById('mailStatus').className = cssClasses.hidden;

           if(this.isEmpty('cname')||this.isEmpty('cemail')||this.isEmpty('cphone')||
           this.isEmpty('ctext'))
           { 
             return;
           }
           if(!this.isValidEmail(document.getElementById('cemail').value,'cemail'))
           return;

           //send data to server
           let data = {name:caller.state.name,email:caller.state.email,
            phone:caller.state.phone,message:caller.state.message};
           axios.post(serverUrl+'/sendemail/',data)
           .then(response=>{
               if(response.status===201)
                {
                    //can : redirect to success page:
                   // caller.props.history.push('/');

                   let elem = document.getElementById('mailStatus');
                   elem.className+=" "+cssClasses.showSuccess;
                   elem.innerText='Email successfully sent.';
                   //reset inputs
                   caller.setState({inputCssClasses:'',name:'',email:'',phone:'',message:''});
                     //remove busy cursor
                    document.getElementById('sendBtn').className=btnCss;
                }
           })
           .catch(err=>{
               //show error message to client
               let elem = document.getElementById('mailStatus');
                   elem.className+=" "+cssClasses.showError;
                   elem.innerText='Server could not send email. Try sometime later.';
                  //remove busy cursor
               document.getElementById('sendBtn').className=btnCss;
           })

         
          
    }
    isEmpty(id)
    {
        if(document.getElementById(id).value.trim()==="")
        {
            //change style to bring attention to input
            document.getElementById(id).className+=" "+cssClasses.invalidInput;
            return true;
        }
        return false; 
    }
    isValidEmail(email,id){

        let arr = email.split('@');
        let elem = document.getElementById(id);
        if(arr.length<2)
        {
            elem.className+=" "+cssClasses.invalidInput;
            return false;
        }
        arr = arr[1].split('.');
        if(arr.length<2)
        {
            elem.className+=" "+cssClasses.invalidInput;
            return false;
        }
        //check that last two items are not empty
        if(arr[arr.length-1].trim()===""||arr[arr.length-2].trim()==="")
        {
            elem.className+=" "+cssClasses.invalidInput;
            return false; 
        }

        return true;
    }

    setOriginalStyle(event,caller)
    {
        event.target.className = caller.state.inputCssClasses;
    }
    valueChanged(elemId,event,caller){
       switch(elemId)
       {
           case 'cname': caller.setState({name:event.target.value});break;
           case 'cemail': caller.setState({email:event.target.value});break;
           case 'cphone': caller.setState({phone:event.target.value});break;
           case 'ctext': caller.setState({message:event.target.value});break;
           default: break;
       }  
    }

    render(){
        return <Layout>
            <div className={cssClasses.contactFormDiv}>
            <div className={cssClasses.pageTitle}>Contact Us</div>
            <form method='post' id='contactForm'  onSubmit={(event)=>this.checkNsubmit(event,this)} >
              <div className={cssClasses.line}>
                  <img src={locationImg} alt="location icon"/>
                  <span>House Number AD 23, Adenta New Site Accra, Ghana - West Africa.</span>
              </div>
              <div className={cssClasses.line}>
                <img src={telephoneImg} alt="telephone icon"/>
               <span >+233 242 805 460 +233 675 908</span>
              </div>
              <div  className={cssClasses.line}>
                  <img src={whatappImg} alt='WhatApp icon'/>
                 <span>+233 242 805 460 </span>
              </div>
             <div className={cssClasses.line}>
                 <img src={emailImg} alt='email icon'/>
               <span>info@sawarisoftware.com </span>
             </div>
             
             <div className={cssClasses.cformInputs}>
             <label className={cssClasses.hidden} id='mailStatus'>Server could not send email. Try sometime later.</label>
             <input type='text' placeholder='Your Name' id='cname' 
             onClick={(event)=>this.setOriginalStyle(event,this)} onChange={(event)=>this.valueChanged('cname',event,this)}/>
             <input type='email' placeholder='Your Email Address' id='cemail' 
             onClick={(event)=>this.setOriginalStyle(event,this)} onChange={(event)=>this.valueChanged('cemail',event,this)} />
             <input type='telephone' placeholder='Your Phone Number' id='cphone' 
             onClick={(event)=>this.setOriginalStyle(event,this)} onChange={(event)=>this.valueChanged('cphone',event,this)}/>
             <textarea  placeholder='Type your message here.' id='ctext' 
             onClick={(event)=>this.setOriginalStyle(event,this)} onChange={(event)=>this.valueChanged('ctext',event,this)}></textarea><br/>
             <button id='sendBtn'>Send Message</button>
             </div>

            </form>
            </div>
        </Layout>
    }
}

export default Contact;