import React from 'react';
import VMenu from './Menu/VMenu';
import cssClasses from '../Menus/Menus.module.css';
import MenuItems from './MenuItems'


class VMenus extends React.Component{
    state={menusDisplay:false,menus:MenuItems};

    render(){
        return <React.Fragment>
        <div className={cssClasses.vmenusContainer}>

        <div className={cssClasses.vmenusBtn} onClick={()=>this.toggleDropDown(this)}>
         <div className={cssClasses.menuItem}></div>
         <div className={cssClasses.menuItem}></div>
         <div className={cssClasses.menuItem}></div>
        </div>
        
        {/*conditional display of menus*/}
        {this.state.menusDisplay ?
          <div className={cssClasses.vmenus+" "+cssClasses.bringMenusIn} 
          id='ddmenus' onMouseLeave={()=>this.hideMenus('ddmenus',this)}>
             {this.state.menus.map((val,index,arr)=>{
                 return <VMenu href={val.link} menuId={val+index} key={val+index}>
                  {val.text}</VMenu>
              })}
         </div>
         : null
        }

        </div>
       
        </React.Fragment> 
    }
    
    hideMenus(id,caller){
              document.getElementById(id).className= cssClasses.vmenus+" "+cssClasses.hideMenus;
              let timeOutId = setTimeout(()=>{
                caller.setState({menusDisplay:!caller.state.menusDisplay});
                clearTimeout(timeOutId);
              },1000);
              
        }

    toggleDropDown(caller){
       caller.setState({menusDisplay:!caller.state.menusDisplay});
    }
}

export default VMenus;