import React from 'react';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';
import html5Css from  '../Html5/Html5.module.css';

const medium = (props) =>{
       return  <div className={cssClasses.container}>
       <div className={cssClasses.blockTitle}>Intermediate Level</div>
       <div className={html5Css.fontSizer}>
       <div className={cssClasses.codeBody}>
        <span className={html5Css.tag}>{'<!DOCTYPE html>'}</span> <br/>
        <span className={html5Css.tag}>{'<html>'}</span><br/>
        <span className={html5Css.tag}>{'<head>'}</span><br/>
        <div className={html5Css.content}>
        <span className={html5Css.tag}>{'<meta '}</span>
        <span className={html5Css.attrib}>charset </span>
        <span>="UTF-8"</span>
        <span className={html5Css.tag}>{">"}</span><br/>

        <span className={html5Css.tag}>{'<meta '}</span>
        <span className={html5Css.attrib}>{' name'}</span>
        <span>="viewport" </span>
        <span className={html5Css.attrib}>content</span>
        <span>="width=device-width, initial-scale=1"</span>
        <span className={html5Css.tag}>{'>'}</span><br/>

        <span className={html5Css.tag}>{'<link '}</span>
        <span className={html5Css.attrib}>rel</span>
        <span>="stylesheet" </span>
        <span className={html5Css.attrib}>href</span>
        <span>="styles.css" </span> <span className={html5Css.tag}>{'>'}</span><br/>

        <span className={html5Css.tag}>{'<script '}</span>
        <span className={html5Css.attrib}>type</span>
        <span>="myscripts.js"</span>
        <span className={html5Css.tag}>{'>'}</span>
        <span className={html5Css.tag}>{'</script>'}</span><br/>

        <span className={html5Css.tag}>{'<title>'}</span>
        <span >{'Page Title'}</span>
        <span className={html5Css.tag}>{'</title>'}</span><br/>
        </div>
        <span className={html5Css.tag}>{'</head>'}</span><br/>
        <span className={html5Css.tag}>{'<body '}</span>
        <span className={html5Css.attrib}>onload</span>
        <span>='showWelcomeMessage();'</span>
        <span className={html5Css.tag}>{'>'}</span>
        <br/>

        <div className={html5Css.content}>

        <span className={html5Css.tag}>{'<div '}</span>
        <span className={html5Css.attrib}> class</span><span>='title'</span>
        <span className={html5Css.tag}>{'>'}</span>
        <span>The World Waits For No One</span>
        <span className={html5Css.tag}>{'</div>'}</span><br/>
        <span className={html5Css.tag}>{'<img '}</span>
        <span className={html5Css.attrib}>src</span>
        <span>="bookImage.jpg" </span>
        <span className={html5Css.attrib}> alt </span>
        <span>="book image"</span>
        <span className={html5Css.tag}>{'/>'}</span><br/>

        <span className={html5Css.tag}>{'<div '}</span>
        <span className={html5Css.attrib}> class</span>
        <span>='others'</span>
        <span className={html5Css.tag}>{' >'}</span><br/>
        <span className={html5Css.tag}>{'<ul>'}</span><br/>
        <div className={html5Css.content}>
         <span className={html5Css.tag}>{'<li>'}</span>
         Money Walks With The Determined
         <span className={html5Css.tag}>{'</li>'}</span><br/>
         <span className={html5Css.tag}>{'<li>'}</span>
         Life's Easy Lessons
         <span className={html5Css.tag}>{'</li>'}</span><br/>
        </div>
        <span className={html5Css.tag}>{'</ul>'}</span>
        <br/><span className={html5Css.tag}>{'</div>'}</span><br/>
        <span>Visit author's website at  </span>
        <span className={html5Css.tag}>{'<a '}</span>
        <span className={html5Css.attrib}> href</span>="http://www.authsam.com"
        <span className={html5Css.tag}>{">"}</span> www.authorsam.com
        <span className={html5Css.tag}>{"</a>"}</span>
        </div>
        <span className={html5Css.tag}>{'</body>'}</span><br/>
        <span className={html5Css.tag}>{'</html>'}</span>
       </div>
       </div>
       </div>
}

export default medium;