import React from 'react';
import reactCss from './ReactComp.module.css';
import cssClasses from '../JavaScriptComp/JavaScriptComp.module.css';

class SimpleReact extends React.Component{
    render(){
         return <div className={cssClasses.container}>
         <div className={cssClasses.blockTitle}>Beginner Level</div>
         <div className={cssClasses.codeBody}>
            <div className={cssClasses.comment}>
                {'/* react imports and components import */'}
            </div>
            <div>
               <span className={cssClasses.keyword}>{'import '}</span>
               <span className={cssClasses.modifier}>{' React '}</span>
               <span className={cssClasses.keyword}>{' from '}</span>
               <span className={cssClasses.path}>{" 'react' "}</span> ; <br/> 
            </div>

            <div>
               <span className={cssClasses.keyword}>{'import '}</span>
               <span className={cssClasses.modifier}>{' CodePlayer '}</span>
               <span className={cssClasses.keyword}>{' from '}</span>
               <span className={cssClasses.path}>{" '../CodePlayer' "}</span> ; <br/> 
            </div>

            <div>
               <span className={cssClasses.keyword}>{'import '}</span>
               <span className={cssClasses.modifier}>{' Simple '}</span>
               <span className={cssClasses.keyword}>{' from '}</span>
               <span className={cssClasses.path}>{" './SimpleReact' "}</span> ; <br/> 
            </div>
            <div>
               <span className={cssClasses.keyword}>{'import '}</span>
               <span className={cssClasses.modifier}>{' Medium '}</span>
               <span className={cssClasses.keyword}>{' from '}</span>
               <span className={cssClasses.path}>{" './MediumReact' "}</span> ; <br/> 
            </div>
            <div className={cssClasses.comment}>
                {'/* component definition */'}
            </div>
            <div>
                <span className={cssClasses.class}>{' class '}</span>
                <span className={cssClasses.component}>{' ReactComp '}</span>
                <span className={cssClasses.class}>{' extends '}</span>
                <span className={cssClasses.component}>{' React.Component '}</span>
                <span>{' { '}</span> <br/>
            </div>
            <div>
              <span className={cssClasses.funcName+" "+cssClasses.indent1}>{' render'}</span>
              <span className={cssClasses.braces}>{'() {'}</span>
            </div>
            <div className={cssClasses.indent2}>
               <span className={cssClasses.class}>{' let '}</span>
               <span className={cssClasses.component}>{' codeBlocks '}</span>
               <span className={cssClasses.braces}>{' = ['}</span>
               <span className={cssClasses.compBrace}>{'<'}</span>
               <span className={cssClasses.modifier}>{' Simple '}</span>
               <span className={cssClasses.compBrace}>{'/>'}</span>
               <span>{','}</span>
               <span className={cssClasses.compBrace}>{'<'}</span>
               <span className={cssClasses.modifier}>{' Medium '}</span>
               <span className={cssClasses.compBrace}>{'/>'}</span>
               <span className={cssClasses.braces}>{' ] '}</span> ;
            </div>
            <div className={cssClasses.indent2}>
               <span className={cssClasses.keyword}>{' return '}</span>
               <span className={cssClasses.compBrace}>{'<'}</span>
               <span className={cssClasses.modifier}>{' CodePlayer '}</span>
               <span className={cssClasses.component}>{' codeBlocks '}</span> = 
               <span className={cssClasses.class}>{' { '}</span>
               <span className={cssClasses.component}>{' codeBlocks '}</span>
               <span className={cssClasses.class}>{' } '}</span>
               <span className={cssClasses.compBrace}>{'/>'}</span>
            </div>

            <div className={cssClasses.braces}>{' } '}</div>

            <div>
                <span className={cssClasses.keyword}>{' export default '}</span>
                <span className={cssClasses.component}>{' ReactComp '}</span> ;
            </div>

         </div>
         </div>
    }
}

export default SimpleReact;