import React,{Component} from 'react';
import './SingleCourse.css';
import Layout from '../../Layout/Layout';
import JavaScriptComp from '../../CodeBlocks/JavaScriptComp/JavaScriptComp'
import Html5Comp from '../../CodeBlocks/Html5/Html5Comp';
import CssComp from '../../CodeBlocks/CSS/CssComp';
import PugComp from '../../CodeBlocks/Pug/PugComp';
import SassComp from '../../CodeBlocks/Sass/SassComp';
import ReactComp from '../../CodeBlocks/ReactBlock/ReactComp';
import VueComp from '../../CodeBlocks/Vue/VueComp';
import AngularComp from '../../CodeBlocks/Angular/AngularComp';
import NodeComp from '../../CodeBlocks/Node/NodeComp';
import TSComp from '../../CodeBlocks/TypeScript/TypeScriptComp';
import GoComp from '../../CodeBlocks/GO/GoComp';
import PyComp from '../../CodeBlocks/Python/PythonComp';
import DjangoComp from '../../CodeBlocks/Django/DjangoComp';

class SingleCourse extends Component{

    state = {selectedSubjectId:this.props.location.courseInfo.subjects.subjects[0]+'0',
             selectedSubject:this.props.location.courseInfo.subjects.subjects[0]};

    showAnimatedContent(){
       
         let subject = this.state.selectedSubject.toLowerCase();
         if(subject.indexOf('javascript')>=0)
            return <JavaScriptComp/>;
          else if(subject==="html5")
            return <Html5Comp/>;
          else if(subject==="css")
            return <CssComp/>;
          else if(subject==="pug")
            return <PugComp/>;
          else if(subject==="sass")
            return <SassComp/>
          else if(subject==="react js")
           return <ReactComp/>;
          else if(subject==="vue js")
            return <VueComp/>;
          else if(subject==="angular")
           return <AngularComp/>;
          else if(subject==="rest"||subject==="node js"||subject==="express")
            return <NodeComp/>;
          else if(subject==="typescript")
            return <TSComp/>
          else if(subject==="go")
            return <GoComp/>;
          else if(subject==="python")
            return <PyComp/>;
          else if(subject==="django")
           return <DjangoComp/>;
          else 
              return <div></div>;
    }

    selectedSubject(subject,index){
       let id = subject+index;
       let el =  document.getElementById(id);
       el.className = 'selectedSubject';
       if(this.state.selectedSubjectId!=='')
         {
            document.getElementById(this.state.selectedSubjectId).className='unSelectedSubject';
         }
         this.setState({selectedSubjectId:id,selectedSubject:subject});
    }

    render(){

        /* the subjects is passed on as subjects in subjects object. 
         Same for courses */ 
        let subjects = this.props.location.courseInfo.subjects.subjects;
        let courseName = this.props.location.courseInfo.courseName.courseName;
        let selectedSubject = subjects[0];
        
        return <Layout>
            <div id='mainDiv'>
                {/* display this drop down for small screens */}
                <div id='smallScreenDD'>
                  <span className='smallScSubHeader'>{courseName}</span>
                  <select className='smallScSubSelect'>
                    {
                      subjects.map((val,index,arr)=>{
                      return <option selected={index==0? true:false} 
                      value={val} key={val+index}  onClick={()=>this.selectedSubject(val,index)}>
                        {val}</option>
                      })
                    }
                  </select>
                </div>
                {/*left menu for displaying course subjects */}
                <div id='leftMenus'>
                    <div id='courseName'>{courseName}</div>
                    <div id='subjects'>
                        {
                          subjects.map((val,index,arr)=>{
                           let clsName = 'unSelectedSubject';
                           if(index===0)
                             clsName = 'selectedSubject';   
                           return <div className={clsName} id={val+index}
                            onClick={()=>this.selectedSubject(val,index)}
                            key={val+index}>{val}</div>;
                          })
                          
                        }
                       
                    </div>
                </div>
                {/* course detail displays selected subject content */}
                <div id='detailDiv'>
                    <div id='subjectTitle'>{this.state.selectedSubject}</div>
                    <div id='displayArea'>
                      {this.showAnimatedContent()}
                    </div>
                </div>
                 {/* campaign message */}
                 <div id='message'>
                    <div id='messageTitle'>Why Learn With Us</div>
                    <div id='reasons'>
                      <ul className='rlist'>
                          <li>We are a team of experienced developers and designers.</li>
                          <li>Our team approach encourages experience sharing and faster learning.</li>
                          <li>We create real world applications together.</li>
                          <li>Opportunity to work on a project of choice and 
                              get constructive criticism from peers.
                          </li>
                          <li>Excellent internship oppportunities.</li>
                          <li>We link you to job opportunities.</li>
                          <li>We provide guidance for self-employment.</li>
                      </ul>
                    </div>
                </div>
            </div>
            </Layout>
    }
}

export default SingleCourse;