import React,{Component} from 'react';
import css from './Tutorial.module.css';
import axios from 'axios';

const serverUrl = "Nodeapp-env.3s9ka22rdf.us-east-2.elasticbeanstalk.com";

class Tutorial extends Component{
  
   //state contains any array of object of type {topic:'',detail:''} from db
   state = {subjectInfo:[],selectedSubject:'',selectedTopic:''};
   
   componentDidMount(){
      //props can come from direct attribute assignment i.e <Tutorial subject='math'/>
        //or through redirect component <Redirect to={{pathname='/tutorial', state={subject:'math'}}}
        let selectedSubject = this.props.subject? this.props.subject : this.props.location.state.subject;
    axios.get(serverUrl+'/subjects/'+selectedSubject).then(response=>{
      let stopic = (response.data.length>0 ? response.data[0].topic:'') ;
      this.setState({subjectInfo:response.data,selectedSubject:selectedSubject,
      selectedTopic:stopic});
      
    })
    .catch(err=>console.log(err));
   }

   showTopicDetail(caller){
      for(let i=0; i<caller.state.subjectInfo.length;i++)
         if(caller.state.subjectInfo[i].topic===caller.state.selectedTopic)
            return <div>{caller.state.subjectInfo[i].detail}</div>;
      return <div></div>;  
   }

   setSelectedTopic(topic, caller){
       caller.setState({selectedTopic:topic});
   }
    
    render(){
       
        return <React.Fragment>
          <div className={css.container}>
          <div className={css.subject}>{this.state.selectedSubject}</div>
          <form>
              <select className={css.topics} >
                {/**topics from subjectInfo.topic */}
                  <option disabled defaultValue>Select Topic</option>
                  {this.state.subjectInfo.map((val,index,arr)=>{
                    return <option key={index+val.topic} value={val.topic} 
                    onClick={()=>this.setSelectedTopic(val.topic,this)}>
                      {val.topic}
                    </option>
                  })}
              </select>
          </form>
                <div className={css.lecture}>
                  <div>  {this.showTopicDetail(this)} </div></div>

          <div className={css.btns}>
            <div className={css.prevbtn}><button>Previous</button></div> 
            <div className={css.nextbtn}><button>Next</button></div>
          </div>
         
          </div>
        </React.Fragment>
    }
}

export default Tutorial;
